import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate from react-router-dom
import styles from './MembersNavigation.module.css';
import { useParams } from 'react-router-dom';

const MembersNavigation = () => {
  const [activeItem, setActiveItem] = useState('Researchers'); // Initialize active item state
  const navigate = useNavigate(); // Initialize navigate function
  const { type } = useParams();

  useEffect(() => {
    if (type) {
      setActiveItem(type.charAt(0).toUpperCase() + type.slice(1));
    }
  }, [type]);

  const handleNavigation = item => {
    setActiveItem(item); // Update the active item
    navigate(`/people/${item.toLowerCase()}`); // Navigate to /people/{item} (convert item to lowercase)
  };

  return (
    <nav className={styles.membersNavigation}>
      <ul className={styles.navigationList}>
        <li
          className={`${styles.navigationItem} ${activeItem === 'Researchers' ? styles.active : ''}`}
          onClick={() => handleNavigation('Researchers')}
        >
          Researchers
        </li>
        <li
          className={`${styles.navigationItem} ${activeItem === 'Alumni' ? styles.active : ''}`}
          onClick={() => handleNavigation('Alumni')}
        >
          Alumni
        </li>
      </ul>
    </nav>
  );
};

export default MembersNavigation;
