import { useState, useEffect } from 'react';

const useCommit = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [commitResponse, setCommitResponse] = useState(null);
  const [token, setToken] = useState(sessionStorage.getItem('token'));

  // 파일의 SHA 값을 가져오는 함수
  const getFileSha = async (owner, repo, path) => {
    try {
      setLoading(true);
      const response = await fetch(`https://api.github.com/repos/${owner}/${repo}/contents/${path}`, {
        headers: {
          Authorization: `token ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error('Error fetching file information');
      }

      const fileData = await response.json();
      return fileData.sha;
    } catch (err) {
      setError(err.message);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // 파일을 업데이트하는 함수
  const updateFile = async (owner, repo, path, newContent, message) => {
    try {
      setLoading(true);

      // 현재 파일의 SHA 값을 가져옴
      const sha = await getFileSha(owner, repo, path);
      if (!sha) return;

      // 파일 업데이트
      const response = await fetch(`https://api.github.com/repos/${owner}/${repo}/contents/${path}`, {
        method: 'PUT',
        headers: {
          Authorization: `token ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: message, // 커밋 메시지
          content: btoa(newContent), // Base64 인코딩된 새로운 파일 내용
          sha: sha, // 기존 파일의 SHA 값
        }),
      });

      if (!response.ok) {
        throw new Error('Error updating file');
      }

      const result = await response.json();
      setCommitResponse(result);
      return result;
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return { loading, error, commitResponse, updateFile };
};

export default useCommit;
