import React, { useEffect } from 'react';
import ResearcherCard from './ResearcherCard';
import styles from './ResearchersSection.module.css';

const ResearchersSection = ({ type, member }) => {
  return (
    <section className={styles.researchersSection}>
      <h1 className={styles.sectionTitle}>{type}</h1>
      <div className={type == 'Professor' ? styles.professorsGrid : styles.researchersGrid}>
        {member.map((researcher, index) => (
          <ResearcherCard key={index} {...researcher} type={type} />
        ))}
      </div>
    </section>
  );
};

export default ResearchersSection;
