import React from 'react';
import PublicationItem from '../../PublicationItem/PublicationItem';
import styles from './Publication.module.css';
import { useState, useEffect } from 'react';
import Hero from '../../Layout/Hero/Hero';
import PublicationNavigation from './MembersNavigation';
const Publication = () => {
  const [publications, setPublications] = useState([]);

  useEffect(() => {
    // 외부 JS 파일 로드
    const script = document.createElement('script');
    script.src = '/data/Publications.js'; // public 경로의 JS 파일 로드
    script.onload = () => {
      if (window.publications) {
        setPublications(window.publications); // window에 저장된 데이터 사용
      }
    };
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script); // 메모리 누수 방지를 위해 script 제거
    };
  }, []);
  return (
    <>
      <Hero name="Publication" />
      <PublicationNavigation />
      <section className={styles.publication}>
        <div className={styles.article}>
          {/* <form className={styles.searchBox}>
          <label htmlFor="searchPublication" className={styles['visually-hidden']}>
            Search Publication
          </label>
          <input type="text" id="searchPublication" placeholder="Search Publication" aria-label="Search Publication" />
        </form> */}
          <div className={styles.publications}>
            <div className={styles.dividerLine} />
            {publications.map((pub, index) => (
              <PublicationItem key={index} {...pub} />
            ))}
          </div>
        </div>
      </section>
    </>
  );
};

export default Publication;
