import React from 'react';
import styles from './GradientPicker.module.css';

const GradientPicker = ({ color1, setColor1, color2, setColor2 }) => {
  // props를 객체 형태로 받습니다.
  return (
    <div className={styles.gradientPicker}>
      <div className={styles.gradientBar} style={{ background: `linear-gradient(to right, ${color1}, ${color2})` }}>
        <input type="color" value={color1} onChange={e => setColor1(e.target.value)} className={styles.colorInput} />
        <input type="color" value={color2} onChange={e => setColor2(e.target.value)} className={styles.colorInput} />
      </div>
    </div>
  );
};

export default GradientPicker;
