import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate from react-router-dom
import styles from './MembersNavigation.module.css';
import { useParams } from 'react-router-dom';

const PublicationNavigation = () => {
  const [activeItem, setActiveItem] = useState('Publications'); // Initialize active item state
  const navigate = useNavigate(); // Initialize navigate function
  const { type } = useParams();

  useEffect(() => {
    if (type) {
      setActiveItem(type.charAt(0).toUpperCase() + type.slice(1));
    }
  }, [type]);

  const handleNavigation = item => {
    setActiveItem(item); // Update the active item
    navigate(`/publication/${item.toLowerCase()}`); // Navigate to /people/{item} (convert item to lowercase)
  };

  return (
    <nav className={styles.membersNavigation}>
      <ul className={styles.navigationList}>
        <li
          className={`${styles.navigationItem} ${activeItem === 'Publications' ? styles.active : ''}`}
          onClick={() => handleNavigation('Publications')}
        >
          Publications
        </li>
        <li
          className={`${styles.navigationItem} ${activeItem === 'Patents' ? styles.active : ''}`}
          onClick={() => handleNavigation('Patents')}
        >
          Patents
        </li>
      </ul>
    </nav>
  );
};

export default PublicationNavigation;
