import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import './App.css';
import Layout from './components/Layout/Layout.jsx';
import Landing from './components/Pages/Landing/Landing.jsx';
import People from './components/Pages/People/People.jsx';
import AboutUs from './components/Pages/AboutUs/AboutUs.jsx';
import Publication from './components/Pages/Publication/Publication.jsx';
import CallbackPage from './components/Pages/Callback/Callback.jsx';
import AdminLayout from './components/Layout/AdminLayout.jsx'; // Admin 전용 레이아웃
const RedirectHandler = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const redirectPath = sessionStorage.getItem('redirect_path');
    if (redirectPath) {
      navigate(redirectPath);
      sessionStorage.removeItem('redirect_path');
    }
  }, [navigate]);

  return null;
};

function App() {
  useEffect(() => {
    let script = document.createElement('script');
    script.src = '/data/cssSetting.js'; // public 경로의 JS 파일 로드
    script.onload = () => {
      if (window.cssSetting) {
        let cssSetting = window.cssSetting;
        for (const section in cssSetting) {
          if (cssSetting.hasOwnProperty(section)) {
            const properties = cssSetting[section];
            Object.entries(properties).forEach(([key, value]) => {
              let formattedString = `${section}${key.charAt(0).toUpperCase() + key.slice(1)}=${value}, `;
              document.documentElement.style.setProperty(
                `--${section}${key.charAt(0).toUpperCase() + key.slice(1)}`,
                value,
              );
            });
          }
        }
      }
    };
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    };
  }, []);

  return (
    <Router>
      <Layout>
        <Routes>
          <Route path="/index.html" element={<RedirectHandler />} />
          <Route path="/" element={<Landing />} />
          <Route path="/landing" element={<Landing />} />
          <Route path="/people" element={<People />} />
          <Route path="/people/:type" element={<People />} />
          <Route path="/about" element={<AboutUs />} />
          <Route path="/publication" element={<Publication />} />
          <Route path="/publication/:type" element={<Publication />} />
          <Route path="/callback" element={<CallbackPage />} />

          {/* Admin 관련 라우트 */}
          <Route path="/admin/*" element={<AdminLayout />}>
            <Route path="landing" element={<Landing />} />
            <Route path="people" element={<People />} />
            <Route path="about" element={<AboutUs />} />
            <Route path="publication" element={<Publication />} />
            <Route path="callback" element={<CallbackPage />} />
          </Route>
        </Routes>
      </Layout>
    </Router>
  );
}

export default App;
