import React from 'react';
import styles from './ResearcherCard.module.css';
import LinkIcon from '@mui/icons-material/Link';
import EmailIcon from '@mui/icons-material/Email';

const ResearcherCard = ({ name, title, email, description, imageSrc, type, affiliations, cvLink }) => {
  const handleCvClick = cvLink => {
    if (cvLink) {
      window.open(cvLink, '_blank', 'noopener,noreferrer');
    }
  };

  return (
    <>
      {type == 'Professor' ? (
        <article className={styles.professorCard}>
          <div className={styles.imageWrapper}>
            <img
              src={imageSrc != 'null' ? imageSrc : '/data/person.png'}
              alt={`${name}`}
              className={styles.professorImage}
            />
          </div>
          <div className={styles.professorInfo}>
            {cvLink != undefined && cvLink != '' && (
              <div className={styles.cvWrapper} onClick={() => handleCvClick(cvLink)}>
                <LinkIcon className={styles.cvIcon} />
              </div>
            )}

            <h2 className={styles.professorName}>{name}</h2>
            <p className={styles.professorTitle}>{title}</p>
            <ul className={styles.professorDetails}>
              <li className={styles.detailItem}>
                <span className={styles.detailValue}>
                  {description.split('\n').map((line, index) => (
                    <React.Fragment key={index}>
                      {line}
                      <br />
                    </React.Fragment>
                  ))}
                </span>
              </li>
              <li className={styles.detailItem}>
                <EmailIcon className={styles.icon} />
                <span className={styles.detailLabel}>CONTACT</span>
                <span className={styles.detailValue}>{email}</span>
              </li>
            </ul>
          </div>
        </article>
      ) : (
        <article className={styles.researcherCard}>
          <div className={styles.imageWrapper}>
            <img
              src={imageSrc != 'null' ? imageSrc : '/data/person.png'}
              alt={`${name}`}
              className={styles.researcherImage}
            />
          </div>
          <div className={styles.researcherInfo}>
            {cvLink != undefined && cvLink != '' && (
              <div className={styles.cvWrapper} onClick={() => handleCvClick(cvLink)}>
                <LinkIcon className={styles.cvIcon} />
              </div>
            )}
            <h2 className={styles.researcherName}>{name}</h2>
            <p className={styles.researcherTitle}>{title}</p>
            <ul className={styles.researcherDetails}>
              <li className={styles.detailItem}>
                <span className={styles.detailValue}>
                  {description.split('\n').map((line, index) => (
                    <React.Fragment key={index}>
                      {line}
                      <br />
                    </React.Fragment>
                  ))}
                </span>
              </li>

              {affiliations != undefined ? (
                <li className={styles.detailItem}>
                  <span className={styles.detailLabelAff}>AFFILIATIONS</span>
                  <span className={styles.detailValue}>{affiliations}</span>
                </li>
              ) : null}
              <li className={styles.detailItem}>
                <EmailIcon className={styles.icon} />
                <span className={styles.detailLabel}>CONTACT</span>
                <span className={styles.detailValue}>{email}</span>
              </li>
            </ul>
          </div>
        </article>
      )}
    </>
  );
};

export default ResearcherCard;
