import React, { useEffect, useState } from 'react';
import styles from './ScrollComponent.module.css'; // CSS 모듈 임포트

const ScrollComponent = ({ children, threshold = 500, isBigger = true }) => {
  const [isVisible, setIsVisible] = useState(false);

  const handleScroll = () => {
    const scrollPosition = window.scrollY;

    if (isBigger ? scrollPosition > threshold : scrollPosition <= threshold) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  useEffect(() => {
    handleScroll();
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [threshold, isBigger]);

  return <div className={`${styles['fade-in']} ${isVisible ? styles.visible : ''}`}>{children}</div>;
};

export default ScrollComponent;
