import React, { useState } from 'react';
import styles from './AdminMenu.module.css';
import GradientPicker from '../GradientPicker/GradientPicker';
import useCommit from '../../Hooks/useCommit';
import { useEffect } from 'react';

const AdminMenu = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [activePanel, setActivePanel] = useState(null);
  const [headerGradientFirst, setHeaderGradientFirst] = useState('#CFC6E8');
  const [headerGradientSecond, setHeaderGradientSecond] = useState('#1A1A2E');
  const [fontColor, setFontColor] = useState('#ffffff');
  const [fontSize, setFontSize] = useState('16px');
  const { loading, error, updateFile } = useCommit();

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  useEffect(() => {
    document.documentElement.style.setProperty('--HeaderGradientFirst', headerGradientFirst);
    document.documentElement.style.setProperty('--HeaderGradientSecond', headerGradientSecond);
    document.documentElement.style.setProperty('--HeaderfontColor', fontColor);
    document.documentElement.style.setProperty('--HeaderfontSize', fontSize);
  }, [headerGradientFirst, headerGradientSecond, fontColor, fontSize]);

  const handlePanelClick = panel => {
    setActivePanel(activePanel === panel ? null : panel);
  };

  const handleCommit = async () => {
    // JavaScript 코드 형식으로 설정
    const newContent = `
  window.cssSetting = {
    Header: {
      GradientFirst: '${headerGradientFirst}',
      GradientSecond: '${headerGradientSecond}',
      color: '${fontColor}',
      fontSize: '${fontSize}',
      logoHighlight: '#FFD700', // 필요한 경우 추가 속성
    },
    Sidebar: {
      color: '#ffffff', // 필요한 경우 추가 속성
      backgroundColor: '#333333',
      width: '250px',
      fontSize: '14px',
    },
  };
  `;

    await updateFile(
      'BearAILab',
      'BEARAILab.github.io',
      'data/cssSetting.js', // 파일 확장자를 .js로 변경
      newContent,
      'Update CSS settings from the Admin Menu',
    );
  };

  return (
    <div className={styles.adminMenuContainer}>
      <div className={`${styles.devCircle} ${isOpen ? styles.open : ''}`} onClick={toggleMenu}>
        DEV
      </div>
      {isOpen && (
        <div className={styles.adminMenu}>
          <h3>Customize Design</h3>
          <div className={styles.menuWrapper}>
            <div className={styles.menuOptions}>
              <button onClick={() => handlePanelClick('header')}>Header Settings</button>
              <button onClick={() => handlePanelClick('footer')}>Footer Settings</button>
              <button onClick={() => handlePanelClick('content')}>Content Settings</button>
            </div>

            <div className={styles.horizontalLine}></div>

            {activePanel === 'header' && (
              <div className={styles.settingsPanel}>
                <label>
                  <GradientPicker
                    color1={headerGradientFirst}
                    setColor1={setHeaderGradientFirst}
                    color2={headerGradientSecond}
                    setColor2={setHeaderGradientSecond} // 수정된 부분
                  />
                </label>
                <label>
                  Font Color:
                  <input type="color" value={fontColor} onChange={e => setFontColor(e.target.value)} />
                </label>
                {/* <label>
                  Font Size:
                  <select value={fontSize} onChange={e => setFontSize(e.target.value)}>
                    <option value="14px">14px</option>
                    <option value="16px">16px</option>
                    <option value="18px">18px</option>
                    <option value="20px">20px</option>
                  </select>
                </label> */}

                <button onClick={handleCommit} disabled={loading}>
                  {loading ? '업데이트 중...' : '업데이트 커밋'}
                </button>
                {error && <p style={{ color: 'red' }}>오류: {error}</p>}
              </div>
            )}

            {activePanel === 'footer' && (
              <div className={styles.settingsPanel}>
                <h4>Footer Settings</h4>
                {/* 푸터 설정을 위한 내용 추가 */}
              </div>
            )}

            {activePanel === 'content' && (
              <div className={styles.settingsPanel}>
                <h4>Content Settings</h4>
                {/* 콘텐츠 설정을 위한 내용 추가 */}
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminMenu;
